.item {
  position: relative;
  max-width: 265px;
  margin-bottom: 10px;
  margin-right: 15px;
  border-radius: 5px;
  transform: scale(0.95);
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  background-color:  #f6fafb;

  &:hover {
    cursor: pointer;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
      1px 4px 6px rgba(0, 0, 0, 0.16);
  }

  .content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 15px;

    .hot {
      position: absolute;
      top: 0%;
      left: 0%;
      z-index: 5;
      width: 49px;
      height: 28px;
      font-size: 15px;
      font-weight: 500;
      text-align: center;
      color: #ffffff;
      background-color: #d9485b;
    }

    .like {
      position: absolute;
      top: 1%;
      left: 87%;
      z-index: 5;
      width: 30px;
      height: 30px;
      background-color: transparent;
      border-radius: 50%;
      transition: background-color 450ms cubic-bezier(0.4, 0, 0.2, 1);
      transition: color 450ms cubic-bezier(0.4, 0, 0.2, 1);

      &:hover .icon {
        color: rgb(255, 217, 4);
      }
      .icon {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        margin-top: 5px;
        color: rgb(192, 192, 192);
      }
    }
    .likeActive {
      .icon {
        color:rgb(255, 217, 4);
      }
    }
    .title {
      font-size: 16px;
      text-align: center;
      display: block;
      white-space: normal;
      color: #555;
    }

    .imgWrapper {
      display: flex;
      justify-content: center;
      height: 130px;
      padding: 10px 0;
      margin-top: 20px;

      .img {
        height: 100%;
      }
    }
    .priceWripper {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 20px;

      .price {
        margin-bottom: 0;
        margin-right: 20px;
        text-align: center;
        font-size: 20px;
        font-weight: 700;
      }
      .cartIcon {
        color: green;
        &:hover {
          color: rgb(4, 182, 28);
        }
      }
    }
    .btn {
      margin: 0 auto;
      width: 150px;
      // border-radius: 7px;
      // color: white;
      // background-color: #32c220;
      // border: none;
    }
  }
}
