.starList {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    padding-left: 0;

    .item {
      width: 15px;
      height: 15px;
      margin-right: 7px;
      list-style: none;

      .icon {
        display: block;
        margin: 0 auto;
        width: 16px;
        height: 16px;
        color: #fccc42;
      }
    }
  }